@import url(https://fonts.googleapis.com/css?family=Dosis|Source+Sans+Pro:400,700&display=swap);
html {
  height: 100%;
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html,
body,
#root {
  position: relative;
  height: 100%; }

html {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%; }

body {
  overflow-x: hidden;
  position: relative;
  background-color: #fff;
  min-height: 100%;
  font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px; }

.header {
  background-color: #002182;
  color: #fff;
  padding: 10px 20px;
  z-index: 100;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 100px; }
  .header .brandWrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 5px 0 35px; }
  .header .brandHeader, .header .appProfile {
    display: flex;
    flex: 1 1; }
  .header .appProfile {
    flex-direction: row-reverse;
    font-size: 0.85rem; }

.appLogo {
  display: inline-block;
  margin: 0 0.75rem 0 0;
  vertical-align: middle; }

.appDropdown {
  display: inline-block;
  width: 9px;
  height: 5px;
  margin: 5px 4px 0; }

.appTitle {
  font-family: "Dosis", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-weight: 600;
  letter-spacing: 0.75px;
  text-transform: uppercase;
  font-size: 1.0rem; }

.headerTabset {
  margin: 1.0rem 0 0;
  font-family: "Dosis", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  text-transform: uppercase;
  font-size: 0.85rem;
  font-weight: 500;
  letter-spacing: 0.5px; }
  .headerTabset .tab {
    cursor: pointer;
    border-bottom: 4px solid #fff;
    display: inline-block;
    padding: 0 12px 12px; }

.sidebar {
  width: 300px;
  background-color: #f0f0f0;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  padding: 0rem 0 0;
  overflow-x: hidden;
  overflow-y: auto;
  border: solid #dfe3eb;
  border-width: 0 1px 0 0;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  z-index: 10; }
  .sidebar.sidebar-pinned {
    width: 55px !important;
    overflow: hidden; }
    .sidebar.sidebar-pinned .btnCollapse {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg); }
    .sidebar.sidebar-pinned .sidebar-overlay {
      display: block; }

.sidebar-overlay {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #f0f0f0; }

.btnCollapse {
  background-color: #f0f0f0;
  border: 0;
  outline: 0;
  padding: 1.25rem 0;
  width: 50px;
  position: absolute;
  z-index: 15;
  right: 0; }

ul {
  display: block;
  list-style: none;
  padding: 0;
  margin: 10px 0 0; }
  ul li {
    display: inline-block;
    width: 100%;
    cursor: pointer;
    padding: 0.75rem 0;
    margin: 0; }
    ul li span {
      display: inline-block;
      font-weight: 500;
      vertical-align: top;
      font-size: 0.80rem; }
    ul li.active {
      background-color: #fff; }
    ul li li {
      padding-left: 25px; }

.sidebarIcon {
  display: inline-block;
  padding: 0 10px; }

.dropdownIcon {
  display: inline-block;
  margin: 4px 0 0 0;
  float: right;
  padding: 0 15px;
  position: relative;
  z-index: -10; }
  .dropdownIcon.dropdownIconInverse {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); }

.shortProfile {
  color: #6c6c6c;
  margin: 0.5rem 0.2rem 0 0.25rem; }
  .shortProfile .avatar {
    width: 35px; }
  .shortProfile .profileMiscShort {
    font-weight: 500;
    font-size: 0.65rem; }
  .shortProfile .profileMiscLong {
    display: none; }
  .shortProfile .profileMeta {
    font-size: 0.75rem; }

.longProfile .avatar {
  width: 45px; }

.longProfile .profileMiscShort {
  display: none; }

.longProfile .firstName, .longProfile .lastName {
  font-size: 1.0rem; }

.longProfile .profileMeta {
  font-size: 0.85rem; }

.longProfile strong {
  font-size: 0.80rem;
  font-weight: 600; }

.avatar {
  display: inline-block;
  margin: 0 0.5rem 0 0; }

.profileMeta {
  display: inline-block;
  vertical-align: top; }

.firstName {
  display: inline-block;
  text-transform: uppercase;
  font-weight: 600;
  margin: 0.34rem 0; }

.lastName {
  display: inline-block;
  font-weight: 600; }

.profileMiscLong span {
  display: inline-block;
  margin: 0 2.0rem 0 0; }

.content {
  height: 100%;
  width: 100%;
  overflow-x: scroll;
  padding: 0.50rem 0 0.5rem 1.2rem; }

.patientForm {
  display: flex;
  margin: 2.0rem 0; }
  .patientForm .form-control {
    flex-grow: 1;
    margin: 0 1rem 0 0; }
  .patientForm input[type="text"] {
    width: 100%;
    border: 2px solid #002182;
    padding: 0.55rem; }

.input-group {
  display: table;
  border-collapse: collapse;
  width: 100%; }

.input-group > div {
  display: table-cell;
  vertical-align: middle; }

.input-group-icon {
  position: relative;
  right: 25px; }
  .input-group-icon img {
    width: 15px; }

.input-group-area {
  width: 100%; }

.input-group input {
  border: 0;
  display: block;
  width: 100%;
  padding-right: 280px; }

.table {
  width: 100%;
  border-collapse: collapse;
  margin: 3.0rem 0 5.0rem; }
  .table thead tr {
    border-top: none; }
  .table thead th {
    font-weight: 700;
    padding: 1.0rem 0.5rem; }
  .table tr {
    border-top: 1px solid rgba(0, 0, 0, 0.05); }
  .table td {
    padding: 0.65rem 0.5rem; }
    .table td.date-cell {
      font-weight: 600;
      padding: 3.0rem 0.5rem 0.5rem; }

.row-severity-h {
  color: #002182;
  font-weight: 700; }

.cell-severity-h,
.cell-severity-m {
  display: inline-block;
  color: #fff;
  width: 20px;
  line-height: 18px;
  text-align: center;
  border-radius: 4px; }

.cell-severity-h {
  background-color: #af0102; }

.cell-severity-m {
  background-color: #f28b11; }

.dropdown-caret {
  cursor: pointer; }

main {
  position: relative;
  width: 100%;
  height: calc(100% - 80px);
  top: 100px;
  padding-left: 300px;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  overflow-x: hidden; }

.main-pinned {
  padding-left: 55px !important; }

